.schedule-card .speaker-avatar:nth-child(n+2) {
    margin-left: -12px;
}

.schedule-card-animation-appear {
    opacity: 0.01;
}

.schedule-card-animation-appear.schedule-card-animation-appear-active {
    opacity: 1;
    -webkit-transition: opacity .1s ease-in;
    -o-transition: opacity .1s ease-in;
    transition: opacity .1s ease-in;
}


